:root {
  --input-height: 4.5rem;
  --main-font: 'Amaranth', sans-serif;
  --main-color: #61DAFB;
}

.input {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: var(--input-height);
  background-color: #2c2c2c;
}

.input .input__logo {
  position: absolute;
  left: 0;
  top: 0;
  width: var(--input-height);
  height: var(--input-height);
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-animation: logo-spin 5s 0s infinite linear;
          animation: logo-spin 5s 0s infinite linear;
}

.input .input__add {
  --height: calc(var(--input-height) / 1.5);
  position: absolute;
  top: calc((var(--input-height) - var(--height)) / 2);
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
  left: 50%;
  width: 45%;
  height: var(--height);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  border-radius: 8px;
  overflow: hidden;
}

.input .input__add * {
  font-family: var(--main-font);
}

.input .input__add-content {
  width: 80%;
  height: 100%;
  padding-left: 1%;
  background-color: #fff;
  color: #1b1b1b;
  font-size: calc(var(--height) / 1.5);
  border: calc((var(--height) / 1000) * 7) solid #3c3c3c;
}

.input .input__add-content:focus {
  -webkit-box-shadow: inset 0px 0px 2px 1px var(--main-color);
          box-shadow: inset 0px 0px 2px 1px var(--main-color);
}

.input .input__add-btn {
  width: 20%;
  height: 100%;
  background-color: #fff;
  color: #3c3c3c;
  text-transform: uppercase;
  font-size: calc(var(--height));
  line-height: calc(var(--height) / 1.1);
  border: 1px solid #000;
}

.input .input__add-btn:hover, .input .input__add-btn:focus {
  -webkit-box-shadow: 0px 0px 1px 0.5px rgba(200, 200, 200, 0.8);
          box-shadow: 0px 0px 1px 0.5px rgba(200, 200, 200, 0.8);
  text-shadow: 0px 0px 3px #fff;
  color: #fff;
  background-color: #3c3c3c;
}

@media only screen and (max-width: 600px) and (min-width: 414px) {
  .input .input__add {
    width: 70%;
  }
}

@media only screen and (max-width: 413px) {
  .input .input__add {
    width: 60%;
  }
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



